
import {defineComponent, defineAsyncComponent, PropType} from "vue";
import {Ability} from '@/types/Ability';
import Popper from "vue3-popper";

const SvgPartBack = defineAsyncComponent(() => import("@/components/elements/svg/part/SvgPartBack.vue"));
const SvgPartHorn = defineAsyncComponent(() => import("@/components/elements/svg/part/SvgPartHorn.vue"));
const SvgPartMouth = defineAsyncComponent(() => import("@/components/elements/svg/part/SvgPartMouth.vue"));
const SvgPartTail = defineAsyncComponent(() => import("@/components/elements/svg/part/SvgPartTail.vue"));

export default defineComponent({
    name: "Ability",
    components: {
        Popper,
        'svg-back': SvgPartBack,
        'svg-horn': SvgPartHorn,
        'svg-mouth': SvgPartMouth,
        'svg-tail': SvgPartTail
    },
    props: {
        item: {
            type: Object as PropType<Ability>,
            required: true
        }
    }
})
